.Story-container {
  background-color: #000;
  color: #fff;
  font-family: "Poppins", sans-serif;
}

/* ===== Hero Section ===== */
.story-hero {
  background-color: #000;
  text-align: center;
  padding: 120px 20px 100px;
}
.story-hero h1 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 64px;
  line-height: 84px;
  letter-spacing: 1%;
  text-align: center;
  color: #ffffff;
  margin: 0 auto;
}

.story-hero h1 span {
  color: #ffffff;
}
.hero-line {
  width: 90vh;
  height: 2px;
  background: #ffffff;
  border: none;
  border-radius: 2px;
  margin: 25px auto;
  opacity: 0.8;
}

.story-hero p {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 34px;
  letter-spacing: 1%;
  text-align: center;
  color: #ccc;
  max-width: 150vh;
  margin: 0 auto 0;
}

.hero-rocket {
  margin-top: 40px;
  width: 100px;
}
/* ===== Founder Section ===== */
.founder-section {
  background: #fff;
  color: #000;
  padding: 100px 10%;
}

.founder-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 40px;
}

.founder-text {
  flex: 1 1 55%;
}

.founder-text h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 64px;
  letter-spacing: 1%;
  color: #000;
  margin-bottom: 30px;
}

/* ===== Message Box ===== */
.founder-message-box {
  background: #fffdf6;
  padding: 40px;
  border-radius: 10px;
  position: relative;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
}

.founder-message-box p {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 32px;
  color: #222;
  margin: 20px 0;
  justify-content: center;
}

/* Quotes */
.quote-start,
.quote-end {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 40px;
  color: #000;
  display: block;
}

.quote-start {
  margin-bottom: 10px;
}

.quote-end {
  text-align: right;
  margin-top: 10px;
}

/* Founder Info */
.founder-info {
  display: flex;
  align-items: center;
  margin-top: 30px;
  gap: 15px;
}

.vertical-line {
  width: 4px;
  height: 45px;
  background: #ccc;
  border-radius: 2px;
}

.founder-info h4 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #000;
  margin: 0;
}

.founder-info p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #555;
  margin: 3px 0 0;
}

/* ===== Image Side ===== */
.founder-image {
  flex: 1 1 35%;
  text-align: right;
  display: flex;
  justify-content: center;
}

.founder-image img {
  width: 280px;
  border-radius: 10px;
}

/* ===== Partnership Section ===== */
.partnership-section {
  background: #0d0d0d;
  text-align: center;
  padding: 100px 20px;
}
.partnership-section  h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 100%;
  letter-spacing: 0;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 40px;
}

.partnership-section p {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0;
  text-align: center;
  color: #FFFFFF;
  max-width: 1200px;
  margin: 0 auto 40px;
}

.partner-logos {
  display: flex;
  justify-content: center;
  gap: 60px;
  flex-wrap: wrap;
}
.partner-logos img {
  height: 100px;
  border-radius: 10px;

}

/* ===== Board Section ===== */
/* ===== Board Section ===== */
.board-section {
  background: #fff;
  color: #000;
  text-align: left; /* ⬅ Align everything to the left */
  padding: 100px 10%;
}

/* Section Heading */
.board-section h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 64px;
  letter-spacing: 1%;
  color: #000;
  margin-bottom: 15px;
  text-align: left;
}

/* Subheading paragraph */
.board-section > p {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 100%;
  letter-spacing: 1%;
  color: #121212;
  max-width: 900px;
  margin-bottom: 50px;
  text-align: left;
}

/* Board Cards Container */
.board-cards {
  display: flex;
  justify-content: flex-start; /* ⬅ Push cards to the left */
  gap: 30px;
  flex-wrap: wrap;
}

/* Individual Board Card */
.board-card {
  background: #FFFDF6;
  border: 1px solid #D2D2D2;

  width: 300px;
  padding: 40px 25px;
  text-align: left; /* ⬅ Keep text inside cards left-aligned */
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Hover effect (optional) */
.board-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.08);
}

/* Avatar */
.board-card .avatar {
  width: 90px;
  height: 90px;
  background: #EDEDED;
  border-radius: 50%;
  margin-bottom: 20px;
}

/* Name */
.board-card h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
  letter-spacing: 1%;
  color: #000;
  margin: 0 0 8px 0;
}
.board-card h4 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 1%;
  color: #333;
 
}


/* Role/Description */
.board-card p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  letter-spacing: 1%;
  color: #121212;
  margin: 0;
}


/* ===== Team Section ===== */
/* ===== Team Section ===== */
.team-section {
  background: #000;
  color: #fff;
  padding: 100px 10%;
}

.team-section h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 100%;
  letter-spacing: 1%;
  margin-bottom: 20px;
}

.team-section  h5 {
  font-family: "Roboto", sans-serif;
  font-weight: 500; /* Medium weight */
  font-style: normal;
  font-size: 24px;
  line-height: 1.25; /* more readable than 100% */
  letter-spacing: 1%;
  color: #FFFFFF; /* keep your original color */
  margin-bottom: 60px; /* as in your current CSS */

}


/* Grid layout */
.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 40px;
  
}

/* Team Card */
.team-card {
  background: #0d0d0d;
  border: 1px solid #333;
  border-radius: 10px;
  width: 300px;
  padding: 40px 25px;
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.team-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.1);
}

/* Avatar */
.team-card .avatar {
  width: 100px;
  height: 100px;
  background: #222;
  border-radius: 50%;
  margin-bottom: 20px;
}

/* Name */
.team-card h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
  letter-spacing: 1%;
  color: #FFFFFF;
  margin: 10px 0 6px 0;
}


/* Designation */
/* Designation */
.team-card h4 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 1%;
  color: #FFFFFF;
  margin-bottom: 10px;
}


/* Description */
.team-card p  {
  font-family: "Roboto", sans-serif;
  font-weight: 400; /* Regular */
  font-style: normal;
  font-size: 14px;
  line-height: 100%; /* or 1.2 for readability */
  letter-spacing: 1%;
  color: #FFFFFF; /* keep as per your original team card style */
  margin: 0;
}

/* ===== Mobile View (max-width: 768px) ===== */
@media (max-width: 768px) {
  /* General */
  .Story-container {
    overflow-x: hidden;
  }

  /* ===== Hero Section ===== */
  .story-hero {
    padding: 100px 20px 60px;
  }

  .story-hero h1 {
    font-size: 24px;
    line-height: 34px;
    text-align: left;
  }

  .hero-line {
    width: 100%;
   
    margin: 15px auto;
  }

  .story-hero p {
    font-size: 14px;
    line-height: 22px;
    max-width: 100%;
    text-align: left;
    
  }

  /* ===== Founder Section ===== */
  .founder-section {
    padding: 60px 20px;
  }

  .founder-content {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }

  .founder-text h2 {
    font-size: 22px;
    line-height: 28px;
    margin-bottom: 15px;
  }

  .founder-message-box {
    padding: 25px 20px;
  }

  .founder-message-box p {
    font-size: 14px;
    line-height: 22px;
  }

  .quote-start,
  .quote-end {
    font-size: 24px;
  }

  .founder-info {
    margin-top: 20px;
  }

  .vertical-line {
    height: 30px;
  }

  .founder-info h4 {
    font-size: 16px;
  }

  .founder-info p {
    font-size: 12px;
  }

  .founder-image {
    justify-content: center;
    margin-top: 20px;
  }

  .founder-image img {
    width: 200px;
  }

  /* ===== Partnerships ===== */
  .partnership-section {
    padding: 60px 20px;
    text-align: left;
  }

  .partnership-section h2 {
    font-size: 22px;
    margin-bottom: 15px;
     text-align: left;
  }

  .partnership-section p {
    font-size: 14px;
     text-align: left;
    line-height: 22px;
  }

  .partner-logos {
    gap: 10vh;
    justify-content: flex-start;  
    padding-left: 5vh;
  }

  .partner-logos img {
    height: 70px;
  }

  /* ===== Board Section ===== */
  .board-section {
    padding: 60px 20px;
  }

  .board-section h2 {
    font-size: 22px;
    line-height: 28px;
  }

  .board-section > p {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 30px;
  }

  .board-cards {
    flex-direction: column;
    gap: 20px;
  }

  .board-card {
    width: 90%;
    padding: 25px 20px;
  }

  .board-card .avatar {
    width: 70px;
    height: 70px;
  }

  .board-card h3 {
    font-size: 18px;
  }

  .board-card h4 {
    font-size: 14px;
  }

  .board-card p {
    font-size: 12px;
  }

  /* ===== Team Section ===== */
  .team-section {
    padding: 60px 20px;
  }

  .team-section h2 {
    font-size: 22px;
  }

  .team-section h5 {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 40px;
  }

  .team-grid {
    grid-template-columns: 1fr;
    gap: 25px;
  }

  .team-card {
    width: 90%;
    padding: 25px 20px;
  }

  .team-card .avatar {
    width: 80px;
    height: 80px;
  }

  .team-card h3 {
    font-size: 18px;
  }

  .team-card h4 {
    font-size: 14px;
  }

  .team-card p {
    font-size: 12px;
  }
}
