.Contact-container {
  background: black;
  color: #fff;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Contact-main-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: stretch;
  padding: 40px;
}

.pin-icon {
  display: inline-flex;
  align-items: center;
  margin-right: 6px;
  vertical-align: middle;
}

.contact-info-map {
  display: flex;
  width: 100%;
  max-width: 1200px;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08);
}

/* Left Panel */
.contact-info {
  flex: 1;
  background: #111;
  color: #fff;
  padding: 40px 50px;   /* more top/bottom padding */
  display: flex;
  flex-direction: column;
}

.contact-info h1 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 28px;
  margin-bottom: 20px;
}


.office-block h2 {
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 8px;
}

.office-block h3 {
  display: flex;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 16px;
  gap: 6px;
  margin-bottom: 6px;
  opacity: 0.9;
}

.office-block p {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.6;
  margin-left: 35px;
  margin-top: 6px; /* spacing between h3 and text */
  opacity: 0.9;
  margin-bottom: 0;
}

/* Right Panel (Map) */
.contact-map {
  flex: 1.5;
  min-height: 400px;
}

.contact-map iframe {
  width: 100%;
  height: 100%;
  border: none;
}

/* Email Section */
.email-section {
  margin-top: 30px;
  padding-top: 20px;
  border-top: 2px solid rgba(255, 255, 255, 0.2);
  position: relative;
  width: 100% ;
  min-height: 20vh;
}


.email-block {
  display: flex;
  align-items: center;
  font-size: 14px;
  font-family: 'Roboto', sans-serif;
  gap: 10px;
  color: #fff;
  margin-top: 10px;
  position: relative;
  z-index: 1; /* keeps text above circles */
}

.email-block a {
  color: #fff;
  text-decoration: none;
  opacity: 0.9;
  transition: opacity 0.2s ease-in-out;
}

.email-block a:hover {
  opacity: 1;
  text-decoration: underline;
}



.email-block p {
  font-size: 14px;
  font-weight: 400;
  opacity: 0.9;
}

/* Mobile Optimization */
@media (max-width: 768px) {
  .Contact-container {
    padding: 0;
  }

  .contact-info-map {
    flex-direction: column;
    box-shadow: none;
    border: none;
    border-radius: 0;
  }

  .contact-info {
    padding: 25px 20px;
    background: #000;
    color: #fff;
  }



  .email-section {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    padding-top: 15px;
    margin-top: 20px;
    margin-bottom: -90px;
  }


  .contact-map {
    width: 100%;
    height: 100%;
    background: #000;
  }

  .contact-map iframe {
    height: 100%;
    width: 100%;
  }
}

